<style>
</style>
<template>
  <div id="role-manager">
    <!---->
    <el-form :inline="true" ref="queryForm" class="demo-form-inline">
          <el-form-item>
            <el-input
              v-model="search"
              class="search_name"
              size="large"
              placeholder="">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              @click="onSearch()"
              class="el-icon-search">
            </el-button>
          </el-form-item>
           <el-form-item>
            <el-button
              type="primary"
              @click="onRest()"
              class="el-icon-delete">
            </el-button>
          </el-form-item>
          <el-form-item>
            <el-button
              class="el-icon-refresh"
              type="primary"
              @click="toAdd()">
            </el-button>
          </el-form-item>
      </el-form>

        <el-row>
            <el-table :data="tableData" style="width: 100%;">
                        <el-table-column prop="name" label="" >
                        </el-table-column>
                        <el-table-column prop="desc" label="" >
                        </el-table-column>

                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" @click="toEdit(scope)"></el-button>
                                <el-button type="danger" size="mini" @click="deleteRole(scope)"></el-button>
                                <el-button type="primary" size="mini" @click="toAuthorize(scope)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <br>
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pagination.pageIndex"
                        :page-sizes="[5, 10, 20, 30, 40]"
                        :page-size=pagination.pageSize
                        layout="total, prev, pager, next"
                        :total=pagination.total>
                    </el-pagination>
        </el-row>

    <el-dialog title="" :visible.sync="dialogFormAdd">
      <el-form :model="role" label-width="100px">
        <el-form-item label="" >
          <el-input v-model="role.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input v-model="role.desc" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormAdd = false"> </el-button>
        <el-button type="primary" @click="add(role)"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogFormEdit">
      <el-form :model="role">
        <el-form-item label="" >
          <el-input v-model="role.name" auto-complete="off"></el-input>
        </el-form-item>
         <el-form-item label="" >
          <el-input v-model="role.desc" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEdit = false"> </el-button>
        <el-button type="primary" @click="edit()"> </el-button>
      </div>
    </el-dialog>


  </div>
</template>

<script>
export default{
    name: 'role-manager',
    data () {
     return {
        tableData: [],
        dialogFormEdit: false,
        dialogFormAdd:false,
        role: {
          id: '',
          name: '',
          desc: ''
        },
        pagination: {
            pageIndex: 1,
            pageSize: 10,
            total: 0,
        },
        search:''
      }
  },
  methods:{
       init () {
        var self = this
         this.$axios({
            method:'post',
            url:'/api/role/getPage',
            data:{"page":this.pagination.pageIndex,"limit":this.pagination.pageSize},
            headers:{
                'Content-Type':'application/json;charset=utf-8'      //
          }
        }).then(res => {
           console.log(res);
           self.pagination.total = res.data.datas.data.total;
           self.tableData = res.data.datas.data.records;
        })
          .catch(function (error) {
            console.log(error)
          })
        },
        handleSizeChange(val) {
                console.log(` ${val} `);
                this.pagination.pageSize = val;
                this.pagination.pageIndex = 1;
                this.init();
        },
        handleCurrentChange(val) {
                 console.log(`: ${val}`);
                this.pagination.pageIndex = val;
                this.init();
        },
        // 
        onSearch() {
            var self = this
            this.$axios({
                method:'post',
                url:'/api/role/getPage',
                data:{"page":this.pagination.pageIndex,"limit":this.pagination.pageSize,"name":this.search},
                headers:{
                    'Content-Type':'application/json;charset=utf-8'      //
            }
            }).then(res => {
            console.log(res);
            console.log(res.data.datas.data.total);
            console.log(res.data.datas.data.records);
            self.pagination.total = res.data.datas.data.total;
            self.tableData = res.data.datas.data.records;
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        // 
        onRest(){
            // model
            this.$refs["queryForm" ].resetFields();
            // init 
            this.init()
        },
        // 
        add(){
            this.$axios({
            method:'post',
            url:'/api/role/insert',
            data:{'name': this.role.name, 'desc':this.role.desc},
            headers:{
                'Content-Type':'application/json;charset=utf-8'      //
            }
        }).then(res => {
          this.$message.success('')
          this.dialogFormAdd = false
          this.init()
        })
          .catch(function (error) {
            console.log(error)
          })
        },
        // 
        toAdd(){
            this.dialogFormAdd = true
            //  model 
            this.role.name = ''
            this.role.desc = ''
        },
        // 
        deleteRole(scope){
            console.log(scope.row)
            if (!scope.row.id) {
                this.tableData.splice(scope.$index, 1)
            } else {
                this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                center: true
            }).then(() => {
                console.log(scope.row.id)

                this.$axios({
                method:'post',
                url:'/api/role/delete',
                data:{'id': scope.row.id},
                headers:{
                    'Content-Type':'application/json;charset=utf-8'      //
                }}).then(res => {
                this.$message.success('')
                this.init()
            }).catch(function (error) {
                console.log(error)
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
            })}).catch(() => {
                this.$message({
                    type: 'info',
                    message: ''
                })
                })}
        },
        // (model )
        toEdit (scope) {
            console.log(scope.row)
            this.role.id = scope.row.id
            this.role.name = scope.row.name
            this.role.desc = scope.row.desc
            this.dialogFormEdit = true
        },
        // 
        edit(){
            this.$axios({
            method:'post',
            url:'/api/role/update',
            data:{'name': this.role.name, 'desc':this.role.desc, 'id':this.role.id},
            headers:{
                'Content-Type':'application/json;charset=utf-8'      //
          }
        }).then(res => {
          this.$message.success('')
          this.dialogFormEdit = false
          this.init()
        })
          .catch(function (error) {
            console.log(error)
          })
        }


  },
  mounted: function () {
      this.init()
  }

}
</script>